
.fue-result {
  &__title {
    margin-bottom: 7rem;
  }

  &__link {
    display: inline-block;
  }

  @include mw1 {
    &__title {
      margin-bottom: 5rem;
    }
  }

  @include mw2 {
    &__title {
      margin-bottom: 3.5rem;
    }
  }

  @include mw3 {
    &__title {
      margin-bottom: 2.5rem;
    }
  }

  @include mw4 {
    &__title {
      margin-bottom: 1.8rem;
    }
  }
}

.fue-result-slider {
  &__images {
    position: relative;
    margin: auto;
    padding-bottom: 43.1%;
    width: 100%;
    max-width: 995px;

    img {
      position: absolute;
      left: 0;
      right: 0;
      top: 0;
      bottom: 0;
    }
  }

  &__navigation {
    position: absolute;
    top: 50%;
    z-index: 5;
    justify-content: space-between;
    width: 100%;
    transform: translateY(-50%);

    button {
      background-color: #fff;

      svg {
        fill: $firstcolor;
      }
    }
  }

  @include mw4 {
    &__images {
      padding-bottom: 0;
      min-height: 275px;
    }
  }
}

.fue-result-thumbs {
  @include mw3 {
    display: none;
  }
}

.fue-result__thumb {
  height: 235px;
  cursor: pointer;

  img {
    opacity: 0.75;
    transition: opacity 0.3s linear;
  }

  @include hover {
    &:hover {
      img {
        opacity: 1;
      }
    }
  }
}

.swiper-slide-thumb-active {
  .fue-result__thumb {
    img {
      opacity: 1;
    }
  }
}
