
.prices {
  &__title {
    margin-bottom: 3rem;
  }

  &__subtitle {
    display: inline-block;
    margin-bottom: 4rem;
    max-width: 79rem;
  }

  @include mw2 {
    &__title {
      margin-bottom: 2.5rem;
    }

    &__subtile {
      line-height: 2;
    }
  }

  @include mw3 {
    &__title {
      margin-bottom: 2rem;
    }

    &__subtitle {
      margin-bottom: 3rem;
    }
  }

  @include mw3 {
    &__subtitle {
      margin-bottom: 2.5rem;
    }
  }
}

.prices-card {
  align-items: flex-start;

  &__item {
    flex-basis: 33.33333%;
    padding: 6.2rem 5rem 5rem;
    box-shadow: $shadow2;
    background-color: #fff;

    &:not(:last-child) {
      margin-right: 30px;
    }

    &:nth-child(even) {
      margin-top: 3.5rem;
    }
  }

  &__img {
    margin: 0 auto;
    width: 100%;
    height: 152px;
    max-width: 192px;
  }

  &__title {
    margin-bottom: 0.5rem;
    font-weight: 600;
  }

  &__cost-value {
    font-size: $fsExtrLarg;
    color: $thirdcol;
  }

  @include mw3 {
    &__item {
      padding: 4.2rem 1.5rem 3rem;

      &:not(:last-child) {
        margin-right: 10px;
      }

      &:nth-child(even) {
        margin-top: 2rem;
      }
    }

    &__cost-value {
      font-size: $fslarge;
    }
  }

  @include mw4 {
    flex-direction: column;
    align-items: center;

    &__item {
      flex-basis: 70%;
      width: 70%;

      &:not(:last-child) {
        margin: 0 0 10px;
      }
    }
  }

  @include mw5 {
    &__item {
      flex-basis: 100%;
      width: 100%;
    }
  }
}
