.motivation {
  &__container {
    justify-content: space-between;
  }

  &__images {
    flex: 0 1 35%;
    margin-right: 5.5rem;
    background-color: $sevencolor;
  }

  &__text {
    flex: 0 1 65%;
    padding: 5.5rem 6rem 4.8rem;
    box-shadow: $shadow3;
  }

  &__title {
    margin-bottom: 3.6rem;
  }

  &__descr {
    line-height: 2.4;

    span {
      font-weight: 700;
    }

    +.motivation__descr {
      margin: 5rem 0;
    }
  }

  @include hd {
    &__images {
      margin-right: 2.5rem;
    }

    &__text {
      padding: 3.5rem 3rem 2.8rem;
    }

    &__title {
      margin-bottom: 2.5rem;
    }

    &__descr {
      +.motivation__descr {
        margin: 3rem 0;
      }
    }
  }

  @include mw1 {
    &__images {
      margin-right: 1.5rem;
    }

    &__text {
      padding: 1.5rem;
    }

    &__title {
      margin-bottom: 1.5rem;
    }

    &__descr {
      +.motivation__descr {
        margin: 1.5rem 0;
      }
    }
  }

  @include mw3 {
    &__container {
      display: block;
    }

    &__images {
      margin: 0 auto 2.3rem;
      width: 70%;
      height: 350px;
    }

    &__text {
      padding: 0;
      box-shadow: none;
      line-height: 1.8;
    }

    &__title {
      text-align: center;
    }
  }

  @include mw4 {
    &__title {
      margin-bottom: 1rem;
    }

    &__descr {
      font-size: $fsnrml;
    }
  }

  @include mw5 {
    &__images {
      width: 100%;
    }

    &__descr {
      line-height: 1.65;
    }
  }
}

.signature {
  align-items: center;
  justify-content: space-between;

  &__text {
    span {
      display: block;
      line-height: 2.2;
    }
  }

  &__full-name {
    font-weight: 700;
  }

  &__position {
    font-size: $fsSeminrml;
  }

  &__img {
    flex: 0 0 228px;
    padding: 0 60px;
    height: 52px;
  }

  @include mw3 {
    &__text {
      span {
        line-height: 1.6;
      }
    }

    &__img {
      flex: 0 0 188px;
      padding: 0 20px;
    }
  }

  @include mw5 {
    display: block;

    &__text {
      margin-bottom: 1rem;
      text-align: center;
    }

    &__img {
      margin: auto;
      width: 130px;
    }
  }
}
