.consultation {
  padding: 215px 0 172px;
  color: $seccolor;
  background-position: center center;
  background-size: cover;
  background-repeat: no-repeat;

  &__inner {
    width: 100%;
    max-width: 60rem;
  }

  &__subtitle {
    margin-bottom: 3.5rem;
    line-height: 1.7;
  }

  &__field {
    &:not(:last-child) {
      margin-right: 1.5rem;
    }
  }

  &--bg1 {
    background-image: url('../images/consultation/main-bg.jpg');
    background-color: $thirdcolor1;
  }

  @include hd {
    padding: 175px 0 129px;
  }

  @include mw1 {
    padding: 130px 0 95px;
  }

  @include mw3 {
    padding: 105px 0 80px;

    &__inner {
      max-width: none;
    }
  }

  @include mw4 {
    padding: 70px 0 55px;

    &__subtitle {
      margin-bottom: 2.5rem;
    }

    &__fields {
      display: block;
    }

    &__field {
      &:not(:last-child) {
        margin-right: 0;
        margin-bottom: 1.5rem;
      }
    }

    &__button {
      width: 100%;
    }
  }
}
