
.stages-list {
  counter-reset: my-counter;

  &__item {
    position: relative;
    justify-content: space-between;

    &::before {
      counter-increment: my-counter;
      content: counter(my-counter);
      position: absolute;
      left: 50%;
      top: 50%;
      display: flex;
      align-items: center;
      justify-content: center;
      overflow: hidden;
      border-radius: 50%;
      width: 6.4rem;
      height: 6.4rem;
      box-shadow: 0 0 0 1.2rem rgba($seccolor, 0.3);
      font-weight: 500;
      font-size: 2.4rem;
      color: $ol;
      background-color: $seccolor;
      transform: translate(-50%, -50%);
    }

    &:not(:last-child) {
      margin-bottom: 4rem;
    }
  }

  &__photo {
    flex: 0 1 50%;
  }

  &__descr {
    flex: 0 1 50%;
    padding: 1.5rem 0 1.5rem 4rem;
  }

  &__text {
    margin: auto;
    max-width: 510px;
  }

  @include mw3 {
    &__item {
      &::before {
        width: 4rem;
        height: 4rem;
        box-shadow: 0 0 0 0.5rem rgba($seccolor, 0.3);
        font-size: 1.8rem;
      }

      &:not(:last-child) {
        margin-bottom: 2rem;
      }
    }

    &__descr {
      padding: 1rem 0 1rem 3rem;
    }

    &__text {
      font-size: $fsnrml;
      line-height: 1.5;
    }
  }

  @include mw4 {
    &__item {
      flex-direction: column;
      align-items: center;
      justify-content: center;

      &::before {
        top: 0;
      }
    }

    &__photo {
      width: 80%;
      height: 220px;
    }

    &__descr {
      padding: 1rem 0;
    }
  }

  @include mw5 {
    &__photo {
      width: 100%;
    }
  }
}